import "../../../styles/auth/Login.scss";
import Layout from "../../layout/Layout";

import "bootstrap/dist/css/bootstrap.css";


import {requestLogin} from "../../../services/AuthService"


//  
import {useEffect, useState , useContext} from "react";
import {GlobalContext} from "../../../contexts/GlobalContextProvider"; //contextAPI

//
import { useNavigate } from 'react-router-dom';
//COOKIE
import Cookies from 'js-cookie';



const Login = () => {
  const navigate = useNavigate();
  const { value,setValue } = useContext(GlobalContext);//  
  
  const [username,setUsername] = useState();
  const [password,setPassword] = useState();
  const loginRequest = async (e)=>{

    const response = await requestLogin(username,password);
    console.log('login......',response);
    
    
    if(response.data.success==="true"){
      const inTenMinutes = new Date(new Date().getTime() + 10 * 60 * 1000);  //   10  

      localStorage.setItem("JWTAUTHENTICATION",response.data.JWTAUTHENTICATION)
  
      navigate('/');  // "/home"  
      window.location.reload();
    }else{

      Cookies.remove('JWTAUTHENTICATION')
    }

    // console.log('login isLogined',Cookies.get('isLogined'))

  }

  return (
    <Layout>
      <section className="login-section">
        <form action=""  onSubmit={(e)=>{e.preventDefault()}}>
          <div className="title">
            <h1>LOGIN</h1>
          </div>
          <div className="">
            <label htmlFor=""> : </label>
            <input className="form-control" type="text" placeholder="ID  " onChange={(e)=>{setUsername(e.target.value)}} />
          </div>
          
          <div className="">
            <label htmlFor=""> : </label>
            <input className="form-control" type="password" placeholder="PASSWORD "onChange={(e)=>{setPassword(e.target.value)}}  />
          </div>
          <div className="find">
            <a href=""> </a>
            <a href=""> </a>
          </div>
          <div>
            <button className="btn btn-primary w-100" onClick={loginRequest}></button>
          </div>
          <div>
            
            <a className="btn btn-secondary text-light w-100" href="/join"></a>
          </div>
        </form>
      </section>
    </Layout>
  );
};

export default Login;
