import Layout from "../../../layout/Layout";
import "../../../../styles/myinfo/lecture/Post.scss";
import { useEffect, useState, useRef } from "react";


const Post = () => {

  const [curSlide, setCurSlide] = useState({})
  const [slideIndex,setSlideIndex] = useState(-1)
  const [slides,setSlides] = useState([])

  // 
  const addSlideHandler=()=>{
    const rightEl = document.querySelector('.right');
    const newDiv = document.createElement("div");
    newDiv.classList.add("slide");

    //
    const controlEl = document.createElement('div')
    controlEl.classList.add('controlbar')

    //
    const canvas = document.createElement('canvas');
    canvas.setAttribute('style','width:100%;height:100%;')

    newDiv.appendChild(controlEl);
    newDiv.appendChild(canvas);
    rightEl.appendChild(newDiv);

    //
    // setCurSlide(newDiv);
    // slideIndex++;
    // slides.post(newDiv);


  }

  return (
      <Layout>
          <section className="myinfo-lecture-list">
              <div className="left">
                  <div className="one tag">
                          <ul>
                              <li classList=""><a href="" > </a></li>
                              <li><a href="/myinfo/lecture/post"> </a></li>
                              <li><a href=""> </a></li>
                              <li><a href=""> </a></li>
                          </ul>
                  </div>
                  <hr />
                  <div className="two tag">
                          <ul className="main-menu">
                              <li>
                                <a href="javascript:void(0)" ></a>
                                  <ul className="sub-menu">
                                    <li><a href="javascript:void(0)" onClick={addSlideHandler}> </a></li>
                                    <li><a href="javascript:void(0)"> </a></li>
                                  </ul>
                                </li>
                              <li>
                                  <a href="javascript:void(0)"> </a>
                                  <ul className="sub-menu">
                                    <li><a href="javascript:void(0)"> </a></li>
                                    <li><a href="javascript:void(0)"> </a></li>
                                  </ul>
                              </li>
                          </ul>
                  </div>
              </div>
              <div className="right">

              </div>
          </section>
      </Layout>
  )

}

export default Post;
